module Yobo.Shared.Errors

open System
open Yobo.Shared.Validation

type AuthenticationError =
    | InvalidLoginOrPassword
    | InvalidOrExpiredToken
    | EmailAlreadyRegistered
    | AccountAlreadyActivatedOrNotFound
    | InvalidPasswordResetKey
    
module AuthenticationError =
    let explain = function
        | InvalidLoginOrPassword -> "Nesprvn vyplnn email nebo heslo."
        | InvalidOrExpiredToken -> "Token nen validn nebo ji vyprela jeho platnost."
        | EmailAlreadyRegistered -> "Tento email je ji v systmu registrovn."
        | AccountAlreadyActivatedOrNotFound -> "Tento et je ji zaktivovn nebo byl zadn neplatn aktivan kl."
        | InvalidPasswordResetKey -> "Kd pro nastaven novho hesla je nesprvn, nebo byl ji pouit."

type DomainError =
    | UserNotActivated
    | LessonCannotBeCancelled
    | LessonCannotBeDeleted
    | LessonCannotBeReserved
    | LessonReservationCannotBeCancelled

module DomainError =
    let explain = function
        | UserNotActivated -> "Uivatel jet nebyl aktivovn."
        | LessonCannotBeCancelled -> "Lekci nelze zruit."
        | LessonCannotBeDeleted -> "Lekci nelze smazat."
        | LessonCannotBeReserved -> "Lekci nelze zarezervovat."
        | LessonReservationCannotBeCancelled -> "Rezervaci na lekci nelze ji zruit."

type ServerError =
    | Exception of string
    | Validation of ValidationError list
    | Authentication of AuthenticationError
    | DatabaseItemNotFound of Guid
    | Domain of DomainError

type ServerResult<'a> = Result<'a, ServerError>

exception ServerException of ServerError

module ServerError =
    let explain = function
        | Exception e -> e
        | Validation errs ->
            errs
            |> List.map ValidationError.explain
            |> String.concat ", "
        | Authentication e -> e |> AuthenticationError.explain
        | DatabaseItemNotFound i -> sprintf "Poloka s ID %A nebyla nalezena v databzi." i
        | Domain e -> e |> DomainError.explain
        
    let failwith (er:ServerError) = raise (ServerException er)
    
    let ofOption err (v:Option<_>) =
        v
        |> Option.defaultWith (fun _ -> err |> failwith)
    
    let ofResult<'a> (v:Result<'a,ServerError>) =
        match v with
        | Ok v -> v
        | Error e -> e |> failwith

    let validate (validationFn:'a -> ValidationError list) (value:'a) =
        match value |> validationFn with
        | [] -> value
        | errs -> errs |> Validation |> failwith